
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
textarea, select, input, button { outline: none; }
body {
	margin: 20px;
}

.restrict-1200 {
	width: 1200px;
	margin: 0 auto;
}

.title {
	font-size: 36px;
	color: #000;
	text-align: center;
	padding-bottom: 20px;
	font-family: sans-serif;
}

.search {
	width: calc(100% - 42px);
	height: 18px;
	font-size: 18px;
	margin: 0 auto;
	padding: 10px;
	display: block;
	border: 1px #ccc solid;
	margin-bottom: 10px;
}

.search:hover, .search:focus {
	border: 1px #666 solid;
}





































.flightList {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: top;
	align-content: flex-start;
}

.flight {
	min-height: 300px;
	width: 200px;
	padding: 20px;
	margin: 10px;
	border: 1px solid #ccc;
	border-radius: 5px;
	flex-grow: 1;
}


/* Flight Title */
.flight h1 {
	font-size: 28px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
}

/* Number */
.flight h2 {
	font-size: 20px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
	padding-bottom: 10px;
}

/* Date */
.flight h3 {
	font-size: 16px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
	padding-top: 5px;
}

.flight .social-bar {
	height: 30px;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: nowrap;	
	padding-top: 10px;
}

.flight .social-bar .social-item {
	height: 100%;
	width: 30px;
	padding: 0px 5px 0px 5px;
}

.flight .social-bar .social-item img {
	width: 100%;
	height: 100%;
	opacity: 0.6;
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	cursor: pointer;
}

.flight .social-bar .social-item:hover img {
	-webkit-filter: none;
	        filter: none;
	opacity: 0.95;
}

.flight h4 {
	font-size: 20px;
	font-family: sans-serif;
	color: #000;
	font-weight: 400;
	text-align: center;
	padding-bottom: 5px;
}

.flight ul {
	width: 100%;
	font-size: 18px;
	list-style: none;
	text-align: center;
	font-family: sans-serif;
	font-weight: 300;
	padding-bottom: 5px;
}

.flight li {
	padding-bottom: 5px;
}

