.flightList {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: top;
	align-content: flex-start;
}

.flight {
	min-height: 300px;
	width: 200px;
	padding: 20px;
	margin: 10px;
	border: 1px solid #ccc;
	border-radius: 5px;
	flex-grow: 1;
}


/* Flight Title */
.flight h1 {
	font-size: 28px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
}

/* Number */
.flight h2 {
	font-size: 20px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
	padding-bottom: 10px;
}

/* Date */
.flight h3 {
	font-size: 16px;
	font-family: sans-serif;
	color: #000;
	font-weight: 300;
	text-align: center;
	padding-top: 5px;
}

.flight .social-bar {
	height: 30px;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: nowrap;	
	padding-top: 10px;
}

.flight .social-bar .social-item {
	height: 100%;
	width: 30px;
	padding: 0px 5px 0px 5px;
}

.flight .social-bar .social-item img {
	width: 100%;
	height: 100%;
	opacity: 0.6;
	filter: grayscale(100%);
	cursor: pointer;
}

.flight .social-bar .social-item:hover img {
	filter: none;
	opacity: 0.95;
}

.flight h4 {
	font-size: 20px;
	font-family: sans-serif;
	color: #000;
	font-weight: 400;
	text-align: center;
	padding-bottom: 5px;
}

.flight ul {
	width: 100%;
	font-size: 18px;
	list-style: none;
	text-align: center;
	font-family: sans-serif;
	font-weight: 300;
	padding-bottom: 5px;
}

.flight li {
	padding-bottom: 5px;
}